<template>
  <div>
    <card-container title="   <br>  ." @openModal="openAttributeHelpModal">
      <button class="selection" @click="selectMode('package')" :class="{active: isPackage}">
        <img src="/static/png/img_mode_package_nor.png" class="img">
        <div class="selection-title">
           
        </div>
        <div class="selection-content">
             <br>
             <br>
            
        </div>
      </button>
      <button class="selection right" @click="selectMode('design')" :class="{active: isDesign}">
        <img src="/static/png/img_mode_self_nor.png" class="img">
        <div class="selection-title">
           
        </div>
        <div class="selection-content">
           ,  <br>
             <br>
            
        </div>
      </button>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'

  export default {
    data () {
      return {
        mode: null
      }
    },
    methods: {
      selectMode (mode) {
        if (this.mode !== mode) {
          this.$emit('selectMode', mode)
          this.mode = mode
        }
      },
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    computed: {
      isPackage () {
        return this.mode === 'package'
      },
      isDesign () {
        return this.mode === 'design'
      }
    },
    components: {
      cardContainer
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../../../style/variable";

  // select-mode
  .selection {
    display: inline-block;
    height: 320px;
    width: 265px;
    border-radius: 8px;
    border: 1px solid $gray;
    text-align: center;
    transition: background-color .2s;
    &.active {
      background-color: $gray;
    }
  }

  .img {
    margin-bottom: 12px;
  }

  .selection-title {
    font-size: 16px;
    margin-bottom: 12px;
  }

  .selection-content {
    font-size: 16px;
  }

  .right {
    float: right;
  }
</style>



