<template>
  <div>
    <card-container title="" sub-title="    .">
      <div>
        <div class="select-object">
          <select-slider title=" ()" v-model="selectedObject" :isIcon="true"
                         :data="coverage.object" @clickIcon="openCoverageHelpModal(objectHelpData)"></select-slider>
        </div>
        <div class="select-surcharge">
          <select-slider title="  " v-model="selectedSurcharge" :isIcon="true"
                         :data="coverage.surcharge" @clickIcon="openCoverageHelpModal(surchargeHelpData)"></select-slider>
        </div>
        <div v-if="!isPackage">
          <item-card title="    "
                     question="     ?"
                     description="         <br>      ."
                     icon="/static/png/img_boxbg_02.png"
                     :value="foreignCarAccident"
                     @click="isForeignCarAccident">
          </item-card>
        </div>
      </div>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import selectSlider from '../../../../../components/select-slider.component'
  import itemCard from './sub-components/item-card.component'

  export default {
    props: {
      isPackage: Boolean,
      object: Object,
      surcharge: Object,
      foreignCarAccident: Boolean
    },
    data () {
      return {
        selectedObject: this.object,
        selectedSurcharge: this.surcharge,
        objectHelpData: {
          title: ' ',
          subTitle: '      <br>  .',
          contents: [
            { img: '/static/png/ic_popup_list_01.png', content: ', , , ,   .' },
            { img: '/static/png/ic_popup_list_02.png', content: ',   300     .' },
            { img: '/static/png/ic_popup_list_03.png', content: '   (2)  .' },
            { img: '/static/png/ic_popup_list_04.png', content: ',     .' }
          ]
        },
        surchargeHelpData: {
          title: ' ',
          subTitle: '       <br>   .',
          contents: [
            { img: '/static/png/ic_popup_list_05.png', content: " ''  ''  " },
            { img: '/static/png/ic_popup_list_06.png', content: '   1   .' },
            { img: '/static/png/ic_popup_list_07.png', content: ',  3       <br>   .' }
          ]
        }
      }
    },
    methods: {
      isForeignCarAccident (ForeignCarAccident) {
        this.$emit('selectedForeignCarAccident', ForeignCarAccident)
      },
      selectObject () {
        this.$emit('selectedObject', this.selectedObject)
      },
      selectSurcharge () {
        this.$emit('selectedSurcharge', this.selectedSurcharge)
      },
      openCoverageHelpModal (data) {
        this.$emit('openCoverageHelpModal', data)
      }
    },
    computed: {
      coverage () {
        return this.$store.getters.getInsurance('coverage')
      }
    },
    watch: {
      selectedObject () {
        this.selectObject()
      },
      selectedSurcharge () {
        this.selectSurcharge()
      }
    },
    components: {
      cardContainer,
      selectSlider,
      itemCard
    }
  }
</script>

<style scoped lang="scss">
  .select-object {
    margin-bottom: 45px;
  }
  .select-surcharge {
    margin-top: 30px;
    margin-bottom: 45px;
  }
</style>



