<template>
  <div class="item-card-wrap">
    <accordion ref="accordion">
      <div slot="header">
        <div class="title">
              
        </div>
        <div class="description">
                    <br>   .
        </div>
        <div class="question">
            ?
        </div>
        <div :class="{'radio-wrapper': isWhatOpen, 'border-bottom': isWhatOpen || isSummeryOpen}">
          <div class="radio">
            <t-radio class="radio" title=", ." v-model="selectedAddWhat"
                     :t-value="true" @click="selectAddWhat">
            </t-radio>
            <t-radio class="radio" title="" v-model="selectedAddWhat"
                     :t-value="false" @click="selectAddWhat">
            </t-radio>
          </div>
        </div>
      </div>
      <div slot="body">
        <!-- whatGroup1 -->
        <div class="question">
          
        </div>
        <div class="radio-wrapper">
          <div class="radio">
            <radio-shape-checkbox
              v-for="(item, index) in whatGroup1Data"
              :key="index"
              class="radio" :title="item.title"
              v-model="selectedWhatGroup1"
              :t-value="item.value">
            </radio-shape-checkbox>
          </div>
        </div>
        <!-- whatGroup2 -->
        <div class="question">
          /
        </div>
        <div class="radio-wrapper">
          <div class="radio">
            <radio-shape-checkbox
              v-for="(item, index) in whatGroup2Data"
              :key="index"
              class="radio" :title="item.title"
              v-model="selectedWhatGroup2"
              :t-value="item.value">
            </radio-shape-checkbox>
          </div>
        </div>
        <!-- whatGroup3 -->
        <div class="question">
          
        </div>
        <div class="radio-wrapper">
          <div class="radio">
            <radio-shape-checkbox
              v-for="(item, index) in whatGroup3Data"
              :key="index"
              class="radio" :title="item.title"
              v-model="selectedWhatGroup3"
              :t-value="item.value">
            </radio-shape-checkbox>
          </div>
        </div>
        <!-- whatGroup4 -->
        <div class="question">
          /
        </div>
        <div class="radio-wrapper">
          <div class="radio">
            <radio-shape-checkbox
              v-for="(item, index) in whatGroup4Data"
              :key="index"
              class="radio" :title="item.title"
              v-model="selectedWhatGroup4"
              :t-value="item.value">
            </radio-shape-checkbox>
          </div>
        </div>
        <t-button width="240" @click="selectWhatData"></t-button>
        <t-button width="240" gray @click="deleteWhatData" class="right"></t-button>
      </div>
    </accordion>
    <div class="folded-what-data-wrapper" v-if="isSummeryOpen">
      <div class="what-data-wrapper">
        <div class="title">
          
          <simple-svg
            filepath="/static/svg/btn_arr_next.svg"
            stroke="none"
            fill="#c0c0c0"
            width="20"
            height="20"
            class="arrow-icon"
          />
        </div>
        <div class="number" @click="openWhat">
          {{selectedWhatGroup1.length}}
        </div>
      </div>
      <div class="what-data-wrapper">
        <div class="title">
          /
          <simple-svg
            filepath="/static/svg/btn_arr_next.svg"
            stroke="none"
            fill="#c0c0c0"
            width="20"
            height="20"
            class="arrow-icon"
          />
        </div>
        <div class="number" @click="openWhat">
          {{selectedWhatGroup2.length}}
        </div>
      </div>
      <div class="what-data-wrapper">
        <div class="title">
          
          <simple-svg
            filepath="/static/svg/btn_arr_next.svg"
            stroke="none"
            fill="#c0c0c0"
            width="20"
            height="20"
            class="arrow-icon"
          />
        </div>
        <div class="number" @click="openWhat">
          {{selectedWhatGroup3.length}}
        </div>
      </div>
      <div class="what-data-wrapper">
        <div class="title">
          /
          <simple-svg
            filepath="/static/svg/btn_arr_next.svg"
            stroke="none"
            fill="#c0c0c0"
            width="20"
            height="20"
            class="arrow-icon"
          />
        </div>
        <div class="number" @click="openWhat">
          {{selectedWhatGroup4.length}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import radioShapeCheckbox from '../../../../components/radio-shape-checkbox.component'
  import tRadio from '../../../../../../components/radio-inline.component'
  import tButton from '../../../../../../components/button.component'
  import accordion from '../../../../../../tdk/accordion/accordion.component'

  export default {
    props: {
      value: Array
    },
    mounted () {
      if (this.value && this.value.length > 0) {
        let selectedWhatGroup1 = []
        let selectedWhatGroup2 = []
        let selectedWhatGroup3 = []
        let selectedWhatGroup4 = []
        for (const value of this.value) {
          selectedWhatGroup1 = [...selectedWhatGroup1, ...this.whatGroup1Data.filter(val => val.value === value).map(val => val.value)]
          selectedWhatGroup2 = [...selectedWhatGroup2, ...this.whatGroup2Data.filter(val => val.value === value).map(val => val.value)]
          selectedWhatGroup3 = [...selectedWhatGroup3, ...this.whatGroup3Data.filter(val => val.value === value).map(val => val.value)]
          selectedWhatGroup4 = [...selectedWhatGroup4, ...this.whatGroup4Data.filter(val => val.value === value).map(val => val.value)]
        }
        this.selectedWhatGroup1 = selectedWhatGroup1
        this.selectedWhatGroup2 = selectedWhatGroup2
        this.selectedWhatGroup3 = selectedWhatGroup3
        this.selectedWhatGroup4 = selectedWhatGroup4
        this.selectedAddWhat = true
        this.isSummeryOpen = true
        this.isWhatOpen = false
      }
    },
    data () {
      return {
        isWhatOpen: false,
        isSummeryOpen: false,
        selectedAddWhat: false,
        selectedWhatGroup1: [],
        selectedWhatGroup2: [],
        selectedWhatGroup3: [],
        selectedWhatGroup4: [],
        whatGroup1Data: [
          {title: '', value: 'injury'},
          {title: '', value: 'disable'},
          {title: '', value: 'dead'},
          {title: '', value: 'abort'},
          {title: '', value: 'burn'}
        ],
        whatGroup2Data: [
          {title: '', value: 'dental'},
          {title: '', value: 'plastic'},
          {title: '', value: 'hitreat'},
          {title: '', value: 'bloodless'}
        ],
        whatGroup3Data: [
          {title: '  ', value: 'brain'},
          {title: '  ', value: 'growth'},
          {title: ' ', value: 'fracture'}
        ],
        whatGroup4Data: [
          {title: ' ', value: 'hiroom'},
          {title: ', ', value: 'hospitalize'}
        ]
      }
    },
    methods: {
      selectAddWhat () {
        if (this.selectedAddWhat) {
          this.$refs.accordion.open()
          this.isWhatOpen = true
        } else {
          this.$refs.accordion.close()
          this.isWhatOpen = false
          this.isSummeryOpen = false
        }
        this.$emit('click', this.selectedAddWhat)
      },
      openWhat () {
        this.$refs.accordion.open()
        this.isWhatOpen = true
        this.isSummeryOpen = false
      },
      selectWhatData () {
        this.$refs.accordion.close()
        this.isWhatOpen = false
        this.isSummeryOpen = true
        const result = [...this.selectedWhatGroup1, ...this.selectedWhatGroup2, ...this.selectedWhatGroup3, ...this.selectedWhatGroup4]
        this.$emit('input', result)
      },
      deleteWhatData () {
        this.selectedWhatGroup1 = []
        this.selectedWhatGroup2 = []
        this.selectedWhatGroup3 = []
        this.selectedWhatGroup4 = []
        this.$emit('input', [])
      }
    },
    components: {
      tRadio,
      radioShapeCheckbox,
      tButton,
      accordion
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../../../../style/variable";

  // what  css
  .item-card-wrap {
    width: 550px;
    padding: 42px 30px;
    box-sizing: border-box;
    border: 2px dashed $gray;
    color: $font-color-2;

    .title {
      font-size: 22px;
      margin-bottom: 12px;
    }

    .description {
      font-size: 16px;
      margin-bottom: 30px;
    }

    .question {
      font-size: 16px;
      margin-bottom: 20px;
      display: flex;
      align-items: center;
      div {
        margin-right: 4px;
      }
      .help-icon {
        cursor: pointer;
        margin-bottom: -5px;
      }
    }
    .radio-wrapper {
      margin-bottom: 42px;
    }
    .border-bottom {
      border-bottom: 1px solid $gray;
    }

    // folded-what-data-wrapper     what  
    .folded-what-data-wrapper {
      display: flex;
      height: 122px;
      align-items: center;
      .what-data-wrapper {
        flex: 1;
      }
      .title {
        font-size: 16px;
        color: $gray-2;
      }
      .arrow-icon {
        display: inline-block;
        transform: translate(0, 3px);
      }
      .number {
        color: $primary-blue;
        font-size: 22px;
        cursor: pointer;
      }
    }
  }
</style>



