<template>
  <div>
    <card-container title="I" sub-title="       .">
      <div>
        <select-slider title=" ()" v-model="selectedPerson" :disabled="true"
                       tip="I      ."
                       :isIcon="true"
                       :data="['2', '1 5/ 3']" @clickIcon="openCoverageHelpModal(person1HelpData)"></select-slider>
      </div>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import selectSlider from '../../../../../components/select-slider.component'

  export default {
    data () {
      return {
        input: {},
        selectedPerson: '1 5/ 3',
        person1HelpData: {
          title: 'I ',
          subTitle: '/  1 5,   3<br>    .',
          contents: [
            {img: '/static/png/ic_popup_list_01.png', content: ', , , ,   .'},
            {img: '/static/png/ic_popup_list_02.png', content: ',   300     .'}
          ]
        }
      }
    },
    methods: {
      selectParson () {
        this.$emit('selectedPerson', this.selectedPerson)
      },
      openCoverageHelpModal (data) {
        this.$emit('openCoverageHelpModal', data)
      }
    },
    watch: {
      selectedPerson (val) {
        this.selectParson()
      }
    },
    components: {
      cardContainer,
      selectSlider
    }
  }
</script>

<style scoped lang="scss">
</style>



