<template>
  <div class="blog-slick">
    <button class="before" @click="before"><</button>
    <slick ref="slick" :options="slickOptions">
      <div v-for="(item, index) of contentsData" :key="index" class="content" @click="move(item.route)">
        <div class="box">
          <img :src="item.imgPath" class="img">
          <div class="content-title" v-html="item.title">
          </div>
          <button v-for="(tag, index) in item.tags" :key="index" class="content-tags">
            #{{ tag }}
          </button>
        </div>
      </div>
    </slick>
    <button class="next" @click="next">></button>
  </div>
</template>

<script>
  import slick from '../tdk/slick/slick-carousel.component'

  export default {
    data () {
      return {
        slickOptions: {
          slidesToShow: 3
          // centerMode: true,
        },
        contentsData: [
          {
            imgPath: '/static/blog/blog1.jpg',
            title: '  <br>  ',
            tags: ['', '', '6'],
            route: '/blog/1'
          },
          {
            imgPath: '/static/blog/blog2.jpg',
            title: '  <br>   ',
            tags: ['', '', '', ''],
            route: '/blog/2'
          },
          {
            imgPath: '/static/blog/blog3.jpg',
            title: '  <br>  ',
            tags: ['', '', '(GA)', '(TM)', ' (CM)'],
            route: '/blog/3'
          },
          {
            imgPath: '/static/blog/blog4.jpg',
            title: '    ',
            tags: ['', '/'],
            route: '/blog/4'
          },
          {
            imgPath: '/static/blog/blog5.jpg',
            title: '    ,<br>    ',
            tags: ['', '', '', '', '()'],
            route: '/blog/5'
          },
          {
            imgPath: '/static/blog/blog6.jpg',
            title: '   <br>: () ',
            tags: ['', '()', '', '', ''],
            route: '/blog/6'
          },
          {
            imgPath: '/static/blog/blog7.jpg',
            title: ' , <br>    ',
            tags: ['', '', '/', '', ''],
            route: '/blog/7'
          },
          {
            imgPath: '/static/blog/blog8.jpg',
            title: '     ,<br>   . ',
            tags: ['', '', '', '  ', ' ', '    '],
            route: '/blog/8'
          },
          {
            imgPath: '/static/blog/blog9.jpg',
            title: '  .<br>   ',
            tags: [' ', '', '/', '11  ', '', '', ''],
            route: '/blog/9'
          },
          {
            imgPath: '/static/blog/blog10.jpg',
            title: '    <br>   ',
            tags: ['', '', '', '', '', '', ''],
            route: '/blog/10'
          }
        ]
      }
    },
    mounted () {
      this.$refs.slick.create()
    },
    methods: {
      move (route) {
        this.$router.push(route)
      },
      before () {
        this.$refs.slick.prev()
      },
      next () {
        this.$refs.slick.next()
      }
    },
    components: {
      slick
    }
  }
</script>

<style scoped lang="scss">
  @import '../style/mixin';
  @import '../style/variable';

  .blog-slick {
    position: relative;

    > button {
      position: absolute;
      font-size: 30px;
      height: 35px;
      top: 0;
      bottom: 0;
      margin: auto;
      background-color: transparent;
      cursor: pointer;

      &.before {
        left: -40px;
      }

      &.next {
        right: -40px;
      }
    }
  }

  .content {
    width: 314px;
    cursor: pointer;
  }

  .box {
    padding: 0 5px;
  }

  .img {
    width: 100%;
    height: 200px;
    margin-bottom: 18px;
  }

  .content-title {
    font-size: 20px;
    margin-bottom: 16px;
  }

  .content-tags {
    @include fontAndColor(12px, #8e8e8e);
    margin-right: 4px;
    border-radius: 20px;
    padding-left: 8px;
    padding-right: 8px;
    margin-bottom: 5px;
    height: 20px;
    background-color: #f2f2f2;
  }
</style>



