<template>
  <div class="content2-wrapper">
    <div class="title">
         (UX)<br>
      (AI)   
    </div>
    <div class="content-wrapper">
      <div class="content" v-for="(item, index) in contentsData" :key="index">
        <img :src="item.imgPath" class="img">
        <div class="content-title">
          {{ item.title }}
        </div>
        <div class="content-sub-title" v-html="item.subTitle">
        </div>
      </div>
    </div>
    <!-- <div v-for="(item, index) in data" :key="index">

    </div> -->
  </div>
</template>

<script>
  export default {
    data () {
      return {
        contentsData: [
          {
            title: '  ',
            subTitle: '11    <br>/  ',
            imgPath: 'static/png/img_main_info02_01.png'
          },
          {
            title: '   ',
            subTitle: '  <br>/  ',
            imgPath: 'static/png/img_main_info02_02.png'
          },
          {
            title: ',    ',
            subTitle: '   <br>    ',
            imgPath: 'static/png/img_main_info02_03.png'
          }
        ]
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '../../../style/mixin';
  @import '../../../style/variable';

  .content2-wrapper {
    width: $min-width;
    margin: 120px auto;
    height: 520px;
    .title {
      @include fontAndColor(40px, #151515);
      text-align: center;
    }
    .content-wrapper {
      height: 430px;
      width: 1020px;
      display: flex;
    }
    .content {
      flex: 1;
      text-align: center;
    }
    .img {
      margin-top: 100px;
      margin-bottom: 30px;
      height: 100px;
    }
    .content-title {
      @include fontAndColor(22px, #424242);
      margin-bottom: 12px;
    }
    .content-sub-title {
      @include fontAndColor(16px, #585858);
    }
  }
</style>



