<template>
  <div class="accident-death-injury">
    <div class="select-death">
      <select-slider title="/  " v-model="selectedDeath" :isIcon="true"
                     :data="deathList" @clickIcon="openCoverageHelpModal(bodyAccidentHelpData)">
      </select-slider>
    </div>
    <div class="select-injury" v-if="injuryList.length > 0">
      <select-slider title="  " v-model="selectedInjury"
                     :data="injuryList">
      </select-slider>
    </div>
  </div>
</template>

<script>
  import selectSlider from '../../../../../../components/select-slider.component'

  export default {
    props: {
      initInjury: Object,
      initDeath: Object
    },
    data () {
      return {
        selectedDeath: this.initDeath || {},
        selectedInjury: this.initInjury || {},
        deathList: [
          {title: '', value: 0},
          {title: '15', value: 15000000},
          {title: '3', value: 30000000},
          {title: '5', value: 50000000},
          {title: '1', value: 100000000}
        ],
        selectedDeathList: {
          '15': 'bodyInjuryGroup1',
          '3': 'bodyInjuryGroup1',
          '5': 'bodyInjuryGroup1',
          '1': 'bodyInjuryGroup2'
        },
        initInjuryList: {
          bodyInjuryGroup1: [
            {title: '15', value: 15000000},
            {title: '2', value: 20000000},
            {title: '3', value: 30000000},
            {title: '5', value: 50000000}
          ],
          bodyInjuryGroup2: [
            {title: '15', value: 15000000},
            {title: '2', value: 20000000},
            {title: '3', value: 30000000},
            {title: '5', value: 50000000},
            {title: '1', value: 100000000}
          ]
        },
        injuryList: [],
        isInitInjury: false,
        bodyAccidentHelpData: {
          title: ' ',
          subTitle: '  ()         <br>/,      .',
          contents: [
            { img: '/static/png/ic_popup_list_01.png', content: '  /   .  , ,     .       .       .' },
            { img: '/static/png/ic_popup_list_11.png', content: ' , ,  .' }
          ]
        }
      }
    },
    methods: {
      selectDeath () {
        if (this.selectedDeath) {
          this.injuryList = []
          const initInjuryKey = this.selectedDeathList[this.selectedDeath.title]
          setTimeout(() => {
            this.injuryList = this.initInjuryList[initInjuryKey] || []
            if (JSON.stringify(this.selectedDeath) === JSON.stringify(this.initDeath)) {
              if (!this.isInitInjury) {
                this.selectedInjury = this.initInjury
                this.isInitInjury = true
              } else {
                this.selectedInjury = {}
              }
            } else {
              this.selectedInjury = {}
            }
          })
          this.$emit('selectedDeath', this.selectedDeath)
        }
      },
      selectInjury () {
        if (this.selectedInjury) {
          this.$emit('selectedInjury', this.selectedInjury)
        }
      },
      openCoverageHelpModal (data) {
        this.$emit('openCoverageHelpModal', data)
      }
    },
    watch: {
      selectedDeath () {
        this.selectDeath()
      },
      selectedInjury () {
        this.selectInjury()
      }
    },
    components: {
      selectSlider
    }
  }
</script>

<style scoped lang="scss">
  .accident-death-injury {
    margin-bottom: 45px;

    .select-injury {
      margin-top: 30px;
    }
  }
</style>



