<template>
  <div>
    <card-container
      title="  <br>?"
      @openModal="openAttributeHelpModal(when1HelpData)"
      help>
      <checkbox
        v-model="selectedWhen1Data"
        v-for="(item, index) in subAttributes.when1"
        :key="index"
        :title="item.title"
        :tValue="item.code"
      ></checkbox>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import checkbox from '../../../../../components/checkbox.component'

  export default {
    props: {
      value: Array
    },
    mounted () {
      this.selectedWhen1Data = this.value
    },
    data () {
      return {
        selectedWhen1Data: [],
        when1HelpData: {
          title: '    ?',
          contents: [
            {title: '  ()  ', content: '       '},
            {title: ' ()  ', content: '         '},
            {title: '   ', content: ' ,   ///,       '},
            {title: '   ', content: '()    '},
            {title: '  ', content: '      '}
          ]
        }
      }
    },
    methods: {
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    watch: {
      selectedWhen1Data (val) {
        this.$emit('input', val)
      }
    },
    computed: {
      subAttributes () {
        return this.$store.getters.getInsurance('subAttributes')
      }
    },
    components: {
      cardContainer,
      checkbox
    }
  }
</script>

<style scoped lang="scss">
</style>



