<template>
  <div>
    <card-container
      title="  <br>   ?"
      @openModal="openAttributeHelpModal(when2HelpData)"
      help>
      <checkbox
        v-model="selectedWhen2Data"
        v-for="(item, index) in subAttributes.when2"
        :key="index"
        :title="item.title"
        :tValue="item.code">
      </checkbox>
    </card-container>
  </div>
</template>

<script>
  import cardContainer from '../../../components/card-container.component'
  import checkbox from '../../../../../components/checkbox.component'

  export default {
    props: {
      value: Array
    },
    data () {
      return {
        selectedWhen2Data: [],
        when2HelpData: {
          title: '  <br>   ?',
          contents: [
            {title: '/', content: '  ,    18   06    <br> '},
            {title: ' ', content: ' ~  5 10, 17 22      <br>,      '},
            {title: '  ', content: '       (  )<br>      '},
            {title: '', content: ', ,        300m<br>             '},
            {title: '  ', content: '         '},
            {title: '/  ', content: '         '}
          ]
        }
      }
    },
    methods: {
      openAttributeHelpModal (data) {
        this.$emit('openAttributeHelpModal', data)
      }
    },
    watch: {
      selectedWhen2Data (val) {
        this.$emit('input', val)
      }
    },
    computed: {
      subAttributes () {
        return this.$store.getters.getInsurance('subAttributes')
      }
    },
    components: {
      cardContainer,
      checkbox
    }
  }
</script>

<style scoped lang="scss">
</style>



